import styles from "./Projects.module.css";
import Aside from "../../components/aside/Aside";
import { projects } from "../../ProjectData";
import FooterMobile from "../../components/main/FooterMobile";

function Projects() {
    return(
        <section className={styles.projectSection} id="project" >
            <Aside/>
            <div className={styles.projectContainer} >
                <div className={styles.projectHeader} >
                    <div className={styles.projectHeaderContent} >
                        <p>Projects</p>
                    </div>
                </div>
                {projects.map((project,index) => (
                <div className={styles.projectCard} key={index} >
                    <div className={styles.projectContent} >
                        <div className={styles.aboutProject} >
                            <h3>{project.title}</h3>
                            <p>{project.description}</p>
                            <div className={styles.projectLinks} >
                                <div className={styles.githubButton} >
                                    <i className="fa-brands fa-github"></i> 
                                    <a href={project.github} >GitHub</a>
                                </div>
                                <div className={styles.demoButton} >
                                    <i className="fa-regular fa-eye"></i> 
                                    <a href={project.host} >Demo</a>
                                </div>
                            </div>
                        </div>
                        <div className={styles.projectView} >
                            <div className={styles.projectGif} >
                                <div className={styles.projectViewer} >
                                    <img src={project.demo} alt="add-link" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                ))}
            </div>
            <footer>
                <div >Developed by Mohit Rajput</div>
                <div>
                    <a href="#project" >Scroll To Top <span>&#8673;</span></a>
                </div>
            </footer>
            <FooterMobile/>
        </section>
    )
}

export default Projects;