import { useRef, useEffect } from 'react';
import {useLocation} from 'react-router-dom';
const ZingTouch = require('zingtouch');

function SwipeableNavigation() {
    const mainRef = useRef(null);
    const location = useLocation();

    useEffect(() => {
        const targetElement = mainRef.current;
        const region = new ZingTouch.Region(targetElement);

        region.bind(targetElement, 'swipe', function(event) {
            const {current} = event.detail.data[0];

            if (current.direction === ZingTouch.DIRECTION_LEFT) {
                switch (location.pathname) {
                    case '/':
                        window.history.pushState({}, '', '/resume');
                        break;
                    case '/resume':
                        window.history.pushState({}, '', '/project');
                        break;
                    case '/project':
                        window.history.pushState({}, '', '/');
                        break;
                    default:
                        break;
                }
            } else if (current.direction === ZingTouch.DIRECTION_RIGHT) {
                switch (location.pathname) {
                    case '/':
                        window.history.pushState({}, '', '/project');
                        break;
                    case '/project':
                        window.history.pushState({}, '', '/resume');
                        break;
                    case '/resume':
                        window.history.pushState({}, '', '/');
                        break;
                    default:
                        break;
                }
            }
        });

        return () => {
            region.unbind(targetElement, 'swipe');
        };
    }, [location]);

    return (
        <main ref={mainRef}>
            
        </main>
    )
}

export default SwipeableNavigation;