import styles from "./Resume.module.css";
import resume from "../../assets/svg/resume.svg";
import resumePdf from "../../assets/static/resume.pdf";

function Resume() {
    return(
        <section className={styles.resumeSection} >
            <div className={styles.resumeContainer} >
                <div className={styles.downloadButtonA} >
                    <a href={resumePdf} download="Resume-Mohit.pdf" target="_blank" rel="noopener noreferrer" >
                        <i className="fa-solid fa-file-arrow-down"></i>
                    </a>
                </div>
                <object type="image/svg+xml" data={resume} >Oops!</object>
                <div className={styles.downloadButtonB}  >
                    <a className={styles.downloadButton} href={resumePdf} download="Resume-Mohit.pdf" rel="noopener noreferrer" >
                        <i className="fa-solid fa-download"></i>
                        <span>Download Resume</span>
                    </a>
                </div>
            </div>
        </section>
    )
}

export default Resume;