import React, { useState } from 'react';
import Navbar from './components/navbar/Navbar';
import Main from './components/main/Main';
import Resume from "./pages/resume/Resume"
import Projects from './pages/projects/Projects';
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import SwipeableNavigation from './components/navbar/SwipeableNavigation';

function App() {

  // currently light theme is not in use
  const [darkTheme, setDarkTheme] = useState(true);

  const router = createBrowserRouter([
    <SwipeableNavigation />,
    {
      path: 'Portfolio/', element: <Navbar darkTheme={darkTheme} setDarkTheme={setDarkTheme} />, children: [
        {
          path: 'home', element: <Main darkTheme={darkTheme} />
        },
        {
          path: 'resume', element: <Resume darkTheme={darkTheme} />
        },
        {
          path: 'projects', element: <Projects darkTheme={darkTheme} />
        }
      ]
    }
  ])

  return (
    <div className='app' >
      <RouterProvider router={router} basename="/Portfolio" />
    </div>
  );
}

export default App;
