import { NavLink, Outlet } from "react-router-dom";
import styles from "./Navbar.module.css";
import logo from "../../assets/static/logo-primary.png";
import homeIcon from "../../assets/static/home.png"
import projectIcon from "../../assets/static/project.png"
import cvIcon from "../../assets/static/cv.png"

function Navbar({darkTheme, setDarkTheme}) {
    return (
        <>
        <nav id={darkTheme? styles.navDarkTheme: styles.navLightTheme} >
            <div className={styles.homeLogo} >
                <div className={styles.homeImage} >
                    <img src={logo} alt="add-link" />
                </div>
            </div>
            <div className={styles.navButtons} >
                <ul className={styles.navList} id={darkTheme? styles.listDarkTheme: styles.listLightTheme} >
                    <li>
                        <NavLink to="home" className = {({isActive}) => (isActive ? styles.activeLink : undefined)} >
                            <img src={homeIcon} alt="add-link" />
                            <span>Home</span>
                        </NavLink> 
                    </li>
                    <li>
                        <NavLink to="projects" className = {({isActive}) => (isActive ? styles.activeLink : undefined)} >
                            <img src={projectIcon} alt="add-link" />
                            <span>Projects</span>
                        </NavLink> 
                    </li>
                    <li>
                        <NavLink to="resume" className = {({isActive}) => (isActive ? styles.activeLink : undefined)}  >
                            <img src={cvIcon} alt="add-link" />
                            <span>Resume</span>
                        </NavLink> 
                    </li>
                </ul>
                {/* <div className={styles.switchThemeButton} >
                    <label htmlFor="theme" className={styles.theme} onClick={() => setDarkTheme(!darkTheme)} >
                        <span className={styles.themeToggleWrap} >
                            <input type="checkbox" className={styles.themeToggle} id="theme" role="switch" name="theme" value="dark" defaultChecked/>
                            <span className={styles.themeIcon} >
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                                <span className={styles.themeIconPart} ></span>
                            </span>
                        </span>
                    </label>
                </div> */}
            </div>
        </nav>
        <Outlet/>
        </>
    )
}

export default Navbar;